import React, { useEffect } from 'react';
import './App.css';
import Header from './components/Header';
import { Route, BrowserRouter as Router, Redirect } from 'react-router-dom';
import Quiz from './components/quiz';
import Auth from './components/Auth';
import Dashboard from './components/Dashboard';
import { AppTypes } from './types';
import { connect } from 'react-redux';


function App(props: AppTypes.IPros) {
  useEffect(() => {
    console.log(props.userAuth)
  });
  const { userAuth } = props;
  // const userAuth = true;
  console.log('props.userAuth', props.userAuth);
  return (
        <Router>
            <div className="app">
              <Header isUserAuth={props.userAuth || false}/>
              {userAuth ? (
                <div>
                  <Redirect from="/login" to="/"/>
                  <Route
                    exact
                    path="/"
                    component={() => <Dashboard/>}
                  />
                  <Route exact path="/test/:id" component={() => <Quiz/>} />
                </div>
              ) : (
                <div>
                  <Redirect from="/" to="/login"/>
                  <Route path="/login" component={() => <Auth/>} />
                </div>
              )}
            </div>
        </Router>
    );
}

const mapStateToProps = (state: any) => {
  return ({
    userAuth: state.authReducer.userAuth
  });
};

export default connect<any, any>(mapStateToProps, null)(App);

